import "./toTop.scss";

const ToTop = () => {
  function toTop() {
    // window.scrollTo(0, 0);
    window.scrollTo({
      behavior: "smooth",
      top: 0,
    });
  }
  return (
    <span onClick={toTop} className="btn btnToTop">
      <i
        className="bi bi-caret-up-fill"
        style={{
          fontSize: "50px",
          transform: "scale(2,3)",
        }}
      ></i>
    </span>
  );
};

export default ToTop;
